.head-slick:hover .slick-next,
.head-slick:hover .slick-prev {
  visibility: visible;
  opacity: 1;
}

.head-slick:hover .slick-next::before,
.head-slick:hover .slick-prev::before {
  visibility: visible;
  opacity: 1;
}

.head-slick .slide-1 {
  width: 100%;
  height: 100vh;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.5) 100%), url("../img//slider1.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.head-slick .slide-2 {
  width: 100%;
  height: 100vh;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.5) 100%), url("../img//slider2.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.head-slick .slick-next,
.head-slick .slick-prev {
  right: 10%;
  width: 40px;
  height: 40px;
  background-color: rgba(191, 191, 191, 0.4);
  border-radius: 100%;
  transition: .3s;
  visibility: hidden;
  opacity: 0;
  z-index: 20;
}

.head-slick .slick-next:focus,
.head-slick .slick-prev:focus {
  background-color: rgba(191, 191, 191, 0.4);
}

.head-slick .slick-next::before,
.head-slick .slick-prev::before {
  content: "\276F";
  font-size: 20px;
  transition: .3s;
  font-weight: 200;
  visibility: hidden;
  opacity: 0;
}

.head-slick .slick-next:hover,
.head-slick .slick-prev:hover {
  background-color: #edb88c;
}

.head-slick .slick-prev {
  left: 10% !important;
}

.head-slick .slick-prev::before {
  content: "\276E";
}

.head-slick .slick-dots {
  top: 92vh;
  bottom: 0;
}

.head-slick .slick-dots li button::before {
  font-size: 10px;
  color: #bfbfbf;
}

.head-slick .slick-dots .slick-active button::before {
  color: #edb88c;
}

.head-slick .slide-1,
.head-slick .slide-2 {
  display: flex !important;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.head-slick .slide-1 h4,
.head-slick .slide-1 h1,
.head-slick .slide-2 h4,
.head-slick .slide-2 h1 {
  color: white;
  text-align: center;
}

.head-slick .slide-1 h4,
.head-slick .slide-2 h4 {
