  font-size: 40px;
}

.head-slick .slide-1 h1,
.head-slick .slide-2 h1 {
  font-family: 'Poppins', sans-serif;
  font-weight: 200;
  font-size: 60px;
}

.head-slick .slide-1 button,
.head-slick .slide-2 button {
  padding: 10px 25px;
  background-color: #edb88c;
  border: none;
  color: #2e2e2e;
  border-radius: 30px;
  transition: .5s;
  position: relative;
  overflow: hidden;
  z-index: 10;
  color: #2e2e2e;
}

.head-slick .slide-1 button::after,
.head-slick .slide-2 button::after {
  content: "";
  background: #e0a06c;
  position: absolute;
  z-index: -5;
  padding: 0.85em 0.75em;
  display: block;
  border-radius: 50%;
  left: -50%;
  right: -50%;
  top: -150%;
  bottom: -150%;
  line-height: 8.34em;
  transform: scale(0, 0);
  transition: all .5s linear;
}

.head-slick .slide-1 button:hover,
.head-slick .slide-2 button:hover {
  color: #1d1d1d;
  transform: translateY(-5px);
}

.head-slick .slide-1 button:hover::after,
.head-slick .slide-2 button:hover::after {
  transform: scale(1, 1);
}

