  justify-content: center;
  align-items: center;
  color: #2e2e2e;
}

.blog-box .box-info button::after {
  content: '»';
  position: absolute;
  opacity: 0;
  right: -20px;
  transition: 0.5s;
  font-size: 18px;
}

.blog-box .box-info button:hover {
  padding-right: 24px;
}

.blog-box .box-info button:hover::after {
  opacity: 1;
  right: 10px;
}

.contact {
  width: 100%;
  min-height: 70vh;
  margin: 200px 0 50px 0;
}

.contact .right-contact img {
  max-width: 100%;
}

.contact .left-contact {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.contact .left-contact h2, .contact .left-contact h5 {
  text-align: center;
}

.contact .left-contact h2 {
  color: #edb88c;
}

.contact .left-contact h5 {
  font-weight: 200;
}

.contact .left-contact .contact-i {
  font-size: 30px;
  color: #edb88c;
  margin: 0 0 30px 0;
}

.contact .left-contact form {
  width: 70%;
}

.contact .left-contact form input {
  width: 70%;
  padding: 10px 20px;
  color: #2e2e2e;
  border-radius: 30px 0px 0px 30px;
  border: 1px solid rgba(0, 0, 0, 0.5);
}

.contact .left-contact form input:focus {
  outline: none;
}

.contact .left-contact form input::placeholder {
  font-style: italic;
}

.contact .left-contact form button {
  width: 30%;
  padding: 10px;
  background-color: #edb88c;
  border-color: transparent;
  border-radius: 0 30px 30px 0;
  color: #2e2e2e;
}

footer {
  width: 100%;
  min-height: 40vh;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/foot-bg.jpg");
  background-position: 100% 50%;
  background-size: cover;
  padding: 30px;
}

footer .first-col, footer .second-col, footer .third-col, footer .fourth-col {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

footer .first-col {
  min-height: 20vh;
}

footer .first-col img {
  max-width: 50%;
  margin-bottom: 10px;
}

footer .first-col p {
  color: white;
  font-weight: 200;
  margin: 20px 0;
}

footer .first-col .foot-icons {
  width: 70%;
  display: flex;
  justify-content: space-between;
}

footer .first-col .foot-icons .foot-i {
  width: 40px;
  height: 40px;
  padding: 10px;
  font-size: 15px;
  background: transparent;
  border-radius: 100%;
  cursor: pointer;
  color: #fdeada;
  border: 1px solid #fdeada;
  transition: .5s;
}

footer .first-col .foot-icons .foot-i:hover {
  background: #e0a06c;
  border: #e0a06c;
}

footer .second-col p, footer .third-col p {
  color: #fdeada;
  font-size: 18px;
  font-weight: 500;
}

footer .second-col ul, footer .third-col ul {
  list-style: none;
  padding: 0;
}

footer .second-col ul li, footer .third-col ul li {
  margin: 10px 0;
}

footer .second-col ul li a, footer .third-col ul li a {
  text-decoration: none;
  color: white;
  font-weight: 300;
  transition: .3s;
}

footer .second-col ul li a:hover, footer .third-col ul li a:hover {
  color: #edb88c;
}

footer .fourth-col {
  min-height: 20vh;
}

footer .fourth-col p {
  color: #fdeada;
  font-size: 18px;
  font-weight: 500;
}

footer .fourth-col .adress {
  margin-top: 10px;
  display: flex;
  align-items: center;
  flex-direction: row;
}

footer .fourth-col .adress .adr-i {
  color: #fdeada;
  font-size: 18px;
  margin-right: 20px;
}

footer .fourth-col .adress p {
  font-weight: 200;
  margin: 0;
}

@keyframes slidebg {
  to {
    background-position: 120px;
  }
}

@keyframes aboutImg {
  25% {
    transform: translate(100px, -10px);
  }
  75% {
    transform: translate(100px, 10px);
  }
}

@keyframes imgAnimation {
  25% {
    transform: translateY(-10px);
  }
  75% {
    transform: translateY(10px);
  }
}

::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  transition: .3s;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #edb88c;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #e0a06c;
}

::-webkit-scrollbar-corner {
  border-radius: 10px;
}
